@import "smart-grid.sass";
@import "normalize.css";
@import "fonts.css";
@import "vars";


* {
	box-sizing: border-box; }

.red {
	width: 100%;
	height: 600px;
	background-color: red; }

.top-section {
	display: flex;
	min-height: 580px;
	height: 100vh;
	width: 100%;
	justify-content: space-between;
	flex-direction: column;
	background-color: red;
	background: url(../img/Main_screen.png) no-repeat center;
	background-size: cover; }

.top-header {
	width: 100%;
	min-height: 60px;
	display: flex;
	justify-content: space-between;
	padding: 12px 20px;

	&-logo {
		display: flex;
		flex-direction: column;
		width: 30%;
		min-height: 45px; }

	&-rewards {
		align-self: center;
		display: flex;
		flex-direction: row;
		justify-content: center;
		position: relative;
		padding: 0 24px;

		&::after {
			position: absolute;
			content: '';
			width: 12px;
			height: 23px;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			background: url(../img/Leaf_right.svg) no-repeat center; }

		&::before {
			position: absolute;
			content: '';
			width: 12px;
			height: 23px;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: url(../img/Leaf_left.svg) no-repeat center; }


		&-item {
			display: flex;
			flex-direction: column;
			font-family: "FiraSansRegular", Arial, sans-serif;
			text-align: center;
			margin-right: 20px;
			color: $middle-gray;

			&:last-child {
				margin-right: 0; }

			span {
				font-family: "BebasNeueBold", Arial, sans-serif;
				font-size: 18px;
				line-height: 22px;
				text-transform: uppercase;
				color: $middle-gray; } } }

	&-phone {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		width: 30%;
		font-family: "RobotoLight", Arial, sans-serif;
		font-size: 14px;
		line-height: 18px;
		color: $dark-gray;

		&-link {
			font-family: "RobotoBold", Arial, sans-serif;
			font-size: 22px;
			line-height: 26px;
			color: $dark-gray;
			text-decoration: none; } } }

.company-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;



	&-name {
		font-family: "BebasNeueBold", Arial, sans-serif;
		font-size: 34px;
		line-height: 34px;
		color: $dark-gray;
		padding-right: 13px;
		margin: 0;
		margin-right: 11px;
		position: relative;

		&::after {
			position: absolute;
			content: '';
			right: 0;
			top: 0;
			width: 2px;
			height: 32px;
			background-color: $dark-gray; } }

	&-description {
		margin: 0;
		font-family: "FiraSansRegular", Arial, sans-serif;
		font-size: 12px;
		line-height: 12px;
		color: $dark-gray;

		span {
			font-family: "FiraSansBold", Arial, sans-serif;
			line-height: 18px;
			font-size: 14px;
			color: $dark-gray; } }

	&-sub {
		font-family: "FiraSansRegular", Arial, sans-serif;
		line-height: 16px;
		font-size: 14px;
		color: $dark-gray; } }


.offer {
	margin: auto 0;
	text-align: center;
	min-height: 320px;

	.main-text {
		margin-bottom: 44px; }

	&-description {
		text-align: center;
		font-family: "FiraSansLight", Arial, sans-serif;
		font-size: 24px;
		color: $dark-gray;
		max-width: 587px;
		margin: 0 auto 110px; }

	&-button {
		font-family: "BebasNeueRegular", Arial, sans-serif;
		font-size: 26px;
		line-height: 32px;
		color: $white;
		cursor: pointer;
		border: none;
		background-color: $red;
		padding: 18px 58px;
		border-radius: 100px;
		text-transform: uppercas; } }


.manager-section {
	min-height: 950px;
	background: url(../img/BG_super.png) no-repeat center;
	text-align: center;
	padding: 80px 0;

	&-header {
		font-family: "BebasNeueBold", Arial, sans-serif;
		font-size: 40px;
		line-height: 50px;
		margin-bottom: 20px;
		color: $white;
		text-transform: uppercase; }

	&-description {
		font-family: "BebasNeueBold", Arial, sans-serif;
		font-size: 26px;
		line-height: 36px;
		color: $white;
		text-transform: uppercase; } }



.main-footer {
	font-family: "BebasNeueBold", Arial, sans-serif;
	color: $white;
	font-size: 26px;
	line-height: 36px;
	width: 100%;
	background: linear-gradient(to right, $dark-blue, $dark-orange);
	padding: 67px 0 20px;
	color: $white;
	text-align: center;
	position: relative;

	&-offer {
		margin: 0 auto;
		text-align: center;
		max-width: 800px;
		padding: 16px 20px 8px;
		background: url(../img/rectangle.svg) no-repeat center; } }


.offer-wrapper {
	width: 750px;
	position: absolute;
	top: 0;
	left: 50%;
	transform: translate(-50%, -50%); }

.social {
	margin: 0 auto;
	width: 500px;
	display: flex;
	justify-content: center;

	.share {
		margin: 0;
		margin-right: 40px; } }

.social-items {
	display: flex;
	flex-direction: row; }

.social-item {
	display: inline-block;
	width: 37px;
	height: 34px;
	background-color: transparent;
	font-size: 0;
	margin-right: 30px;

	&:last-child {
		margin-right: 0; } }
